import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';


let 박스 = styled.div`
  padding : 20px;
`;

let 제목 = styled.h4`
  font-size : 25px;
  color : ${ props => props.색상 }
`;

// // 이전 문법
// class Detail2 extends React.Component {

//   componentDidMount() {

//   }

//   componentWillUnmount() {

//   }
// }



function Detail(props) {

  let [hide, hideChange] = useState(true);
  let [temp, tempChange] = useState('');

  useEffect(() => {
    let 타이머 = setTimeout(() => {
      hideChange(false)
    }, 2000);
    return () => { clearTimeout(타이머) }
  },[]);

  let { id } = useParams(); // {사용자가 입력한 파라미터 값}
  let history = useHistory();

  let 찾은상품 = props.shoes.find(function(상품){
    return 상품.id == id
  })

  return (
    <div className="container">
      {/* <박스>
        <제목 className="red" >Detail</제목>
      </박스> */}

      {/* {temp}
      <input onChange={ (e) => { tempChange(e.target.value) } }/> */}

      {
        hide === true ? 
          <div className="my-alert2">
            <p>재고가 얼마 남지 않았습니다</p>
          </div>
        : null
      }


      <div className="row">
        <div className="col-md-6">
          <img src={ "https://codingapple1.github.io/shop/shoes" + (찾은상품.id + 1) +".jpg" } width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{ 찾은상품.title }</h4>
          <p>{ 찾은상품.content }</p>
          <p>{ 찾은상품.price }</p>

          <Info
            재고={props.재고}
          />

          <button className="btn btn-danger" onClick={ () => { props.재고변경([9,11,12]) } }>주문하기</button> 
          <button className="btn btn-danger" onClick={ () => {history.goBack();} } >뒤로가기</button> 
        </div>
      </div>
    </div> 
  );
}

function Info(props) {
  return (
    <p>재고 : { props.재고[0] }</p>
  )
}

export default Detail;